@import (less) "color.less";

.icheckbox-container {
	margin-right: 0.625em;
}
.icheckbox-container.checked {
  .checkbox-label {
    color: @green !important;
  }
}
.icheckbox-container.disabled {
  .checkbox-label {
    color: @light_gray !important;
  }
}
//TODO è NECESSARIO AGGIORNARE L'URL DELLA IMMAGINE, IN ACCORDO CON LA RISOLUZIONE DEI PERCORSI PER I PACCHETTI NPM
.icheckbox_intesa {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	margin: 0;
	padding: 0;
	width: 16px;
	height: 17px;
	background: url(/cjlib/assets/images/intesa.png) no-repeat;
	border: none;
	background-position: -36px 0;
	input[type="checkbox"] {
		margin: 4px 4px 0 0 !important;
		cursor: pointer;
		opacity: 0;
		&:disabled {
			cursor: auto;
		}
	}
	&.yellowborder {
		background-position: -215px 0;
	}
}
.icheckbox_intesa.checked {
	background-position: -18px 0;
	&.yellowborder {
		background-position: -233px 0;
	}
}
.icheckbox_intesa.disabled {
	background-position: -36px 0;
	cursor: default;
	&.yellowborder {
		background-position: -180px 0;
	}
}
.icheckbox_intesa.checked.disabled {
	background-position: -54px 0;
	&.yellowborder {
		background-position: -197px 0;
	}
}
.checkbox-label {
	color: @gray;
	font-size: 0.750em;
	margin: 0 0 0 5px !important;
}
.input-checkbox {
	position: absolute;
	left: -1px;
	top: -2px;
}
//TODO è NECESSARIO AGGIORNARE L'URL DELLA IMMAGINE, IN ACCORDO CON LA RISOLUZIONE DEI PERCORSI PER I PACCHETTI NPM
@media (-o-min-device-pixel-ratio:5/4) {
	.icheckbox_intesa {
		background-image: url(/cjlib/asets/images/intesa@2x.png);
		-webkit-background-size: 645px 19px;
		background-size: 645px 19px;
	}
}
//TODO è NECESSARIO AGGIORNARE L'URL DELLA IMMAGINE, IN ACCORDO CON LA RISOLUZIONE DEI PERCORSI PER I PACCHETTI NPM
